import { Component, OnInit } from '@angular/core';
import {WorkItemService} from './work-item.service';
import {WorkItem} from './work-item';
import {tap} from 'rxjs/operators';

@Component({
  selector: 'app-work-items',
  template: `
    <div class="results" infinite-scroll (scrolled)="onScrolledDown()" (scrolledUp)="onScrolledUp()" infiniteScrollThrottle="2000">
      <div *ngFor="let workItem of workItems">
        <h1>{{workItem.title}}</h1>
        <div [innerHTML]="workItem.body | toMarkdown "></div>
      </div>
    </div>
  `,
  styles: []
})
export class WorkItemsComponent implements OnInit {

  currentPage = 0;
  fileUri: string;
  workItems: WorkItem[] = [];

  constructor(private daemonService: WorkItemService) { }

  ngOnInit(): void {
    this.getWorkItems();
  }

  getWorkItems(): void {
    this.daemonService.getWorkItems(this.currentPage)
    .subscribe(items => items.forEach(
      item => {
        this.workItems.push(item);
      }
    ));
  }

  onScrolledDown() {
    console.log('scrolled down');
    this.currentPage++;
    this.getWorkItems();
  }

  onScrolledUp() {
    console.log('scrolled up');
  }
}
