import { Injectable } from '@angular/core';
import {Observable, of} from 'rxjs';
import {WorkItem} from './work-item';
import {HttpClient} from '@angular/common/http';
import {delay, switchMap, tap} from 'rxjs/operators';
import {environment} from '../../environments/environment';

const WORKITEMS = [
  new WorkItem('test', 'some text body')
];

class PreFetchResponse {
  constructor(readonly identifier: string, readonly message: string) {}
}

@Injectable({
  providedIn: 'root'
})
export class WorkItemService {

  currentIdentifier: PreFetchResponse;
  constructor(private http: HttpClient) {}

  private fetchWorkItems(): Observable<WorkItem[]> {
      return this.http.get<PreFetchResponse>(`http://${environment.daemonHostUrl}/workItems?uri=file:///home/dom/Workspace/AxonFramework`)
        .pipe(
          tap(result => {
            console.log(result.identifier);
          }),
          delay(2000),
          switchMap((value) => {
            this.currentIdentifier = value;
            return this.fetchResults(value);
          }
      ));
  }

  fetchResults(value: PreFetchResponse, page: number = 0) {
    return this.http.get<WorkItem[]>(`http://${environment.daemonHostUrl}/workItems/${value.identifier}?page=${page}`).pipe(
      tap(result => {
          console.log(result);
      })
    );
  }

  public getWorkItems(page: number = 0): Observable<WorkItem[]> {
    if (this.currentIdentifier !== undefined) {
      return this.fetchResults(this.currentIdentifier, page);
    } else {
      return this.fetchWorkItems();
    }
  }

}
